.icon {
  display: block;
  height: 24px;
  width: 24px;
  background-image: url(../images/icons/icon-burger-menu.svg);
  background-size: cover;

  @include hover(transform, scale($hover-scale-icon-value));

  @include onTablet {
    height: 32px;
    width: 32px;
  }

  &--phone {
    background-image: url(../images/icons/icon-phone.svg);

    &::before {
      content: "+1 234 5555-55-55";
      display: block;
      position: absolute;
      top: 100%;
      right: 10%;
      line-height: 32px;
      width: max-content;

      color: $main-text-color;
      text-decoration: none;
      letter-spacing: 2px;

      transition: transform $effectDuration;
      transform: scale(0);
      transform-origin: top;

      @include  onTablet {
        top: initial;
        right: 100%;
        padding-right: 10px;
        font-size: 12px;
        letter-spacing: 1px;
        transform-origin: right;
      }

      @include onDesktop {
        font-size: inherit;
        letter-spacing: 2px;
      }
    }

    &:hover::before {
      transform: scale(1);
    }
  }

  &--menu {
    background-image: url(../images/icons/icon-burger-menu.svg);

    &:hover {
      background-image: url(../images/icons/icon-burger-menu-hover.svg);
    }
  }

  &--close {
    background-image: url(../images/icons/icon-close.svg);
  }
}
